      title: "Achievements",
      items: [
        {
          text: "FatCake",
          active: true,
        },
        {
          text: "Achievements",
        },
      ],
    };
  },
  mounted: async function() {
    this.initPage();
  },
  watch: {
    walletConnected () {
      this.initPage();
    }
  },
  computed: {
    walletConnected() {
      return this.$store.getters['wallet/validWallet'];
    },
  },
  methods: {
    async initPage() {
      this.loading = true;
      if (this.$store.getters['wallet/validWallet']) {
        this.activeAchievement = await this.getAchievement(this.$store.state.wallet.currentWallet);
        this.loading = false;
      }
    },
    async getAchievement(wallet) {
      let balance = await this.getBalance(wallet);

      if (balance >= 1000000000) {
        return {
          name: "Fat Whale",
          tokens: balance,
          image: "https://i.ibb.co/db3KkW9/whale-fat.png"
        };
      }
      if (balance >= 500000000) {
        return {
          name: "Golden Whale",
          tokens: balance,
          image: "https://i.ibb.co/0CsJWfR/whale-golden.png"
        };
      }
      if (balance >= 250000000) {
        return {
          name: "Baby Whale",
          tokens: balance,
          image: "https://i.ibb.co/BtkhR6t/whale-baby.png"
        };
      }
      if (balance >= 100000000) {
        return {
          name: "Investor",
          tokens: balance,
          image: "https://i.ibb.co/NxqqJpY/investor.png"
        };
      }
      if (balance >= 10000000) {
        return {
          name: "DayTrader",
          tokens: balance,
          image: "https://i.ibb.co/SvgxY5s/trader.png"
        };
      }

      return {
          name: "No Achievement",
          tokens: balance,
          image: "https://i.ibb.co/0XMYr5Y/2222628-200.png"
        };
    },
    async getBalance(wallet) {
      Contract.setProvider("https://bsc-dataseed.binance.org");
      let contractAddress = '0xb7dba4c673bedb174dc3ff7ec65d17c863d39b16';

      let contract = new Contract(JSON.parse(this.contractAbi), contractAddress);
      let decimals = await contract.methods.decimals().call();
      
      try {
          let result = await contract.methods.balanceOf(wallet).call();
          let balance = parseFloat(result) / Math.pow(10, decimals);
          return balance;
      } catch (error) {
          console.log(error);
          return 0;
      }
    },
  },
  middleware: "authentication",
};
</script>

<template>
  <Layout>

    <div class="row" v-if="$store.getters['wallet/validWallet']">
      <h5>Your Achievement</h5>

      <Achievement 
        :name="activeAchievement.name"
        :image="activeAchievement.image"
        :tokens="activeAchievement.tokens"
        :active="true"
        v-if="!loading"
      />
      <div
        class="col-xl-3 col-sm-6"
        v-if="loading"
      >
        <div class="card text-center">
          <div class="card-body">
            <div class="clearfix"></div>
            <div class="mb-4">
              <br/>
              <b-spinner
                class="m-2"
                variant="primary"
                role="status"
            ></b-spinner>
            </div>
            <p class="text-muted mb-2">Loading achievement..</p>
          </div>
        </div>
      </div>
    </div>

    <PageHeader :title="title" :items="items" />

    <div class="row">
      <Achievement 
        name="DayTrader"
        image="https://i.ibb.co/SvgxY5s/trader.png"
        tokens="10,000,000"
      />
      <Achievement 
        name="Investor"
        image="https://i.ibb.co/NxqqJpY/investor.png"
        tokens="100,000,000"
      />
      <Achievement 
        name="Baby Whale"
        image="https://i.ibb.co/BtkhR6t/whale-baby.png"
        tokens="250,000,000"
      />
      <Achievement 
        name="Golden Whale"
        image="https://i.ibb.co/0CsJWfR/whale-golden.png"
        tokens="500,000,000"
      />
      <Achievement 
        name="Fat Whale"
        image="https://i.ibb.co/db3KkW9/whale-fat.png"
        tokens="1,000,000,000"
      />
    </div>

    <div class="row" v-if="!$store.getters['wallet/validWallet']">
      <div class="col-12 col-md-12 col-lg-8 col-xl-5" >
        <hr style="color: white;" />
        <div class="card">
            <div class="card-body">
                <h4 class="card-title">FatCake Achievements</h4>
                <p class="card-title-desc">
                Please connect your wallet to recieve your achievement.
                </p>

                <div class="">
                    <b-alert variant="danger" show>Not connected!</b-alert
                    >
                </div>
            </div>
        </div>
    </div>
    </div>
    <!-- end row -->
    <!-- end row -->
  </Layout>
</template>
